import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

const router = new Router({
  mode: 'hash',
  scrollBehavior () {
    return { y: 0 }
  },
  routes: [
    /* index */
    { path: '/', name: 'index', component: require('demo/index.vue') },
    /* demo */
    { path: '/demo/rate', name: 'demo-rate', component: resolve => require(['demo/rate.vue'], resolve) },
    { path: '/demo/icons', name: 'demo-icons', component: resolve => require(['demo/icons.vue'], resolve) },
    { path: '/demo/field/input', name: 'demo-field-input', component: resolve => require(['demo/field/input.vue'], resolve) },
    { path: '/demo/field/textarea', name: 'demo-field-textarea', component: resolve => require(['demo/field/textarea.vue'], resolve) },
    { path: '/demo/field/select', name: 'demo-field-select', component: resolve => require(['demo/field/select.vue'], resolve) },
    { path: '/demo/modal/dialog', name: 'demo-modal-dialog', component: resolve => require(['demo/modal/dialog.vue'], resolve) },
    { path: '/demo/modal/alert', name: 'demo-modal-alert', component: resolve => require(['demo/modal/alert.vue'], resolve) },
    { path: '/demo/modal/tip', name: 'demo-modal-tip', component: resolve => require(['demo/modal/tip.vue'], resolve) },
    { path: '/demo/nav/sider-nav', name: 'demo-sider-nav', component: resolve => require(['demo/nav/sider-nav.vue'], resolve) },

    /* doc */
    { path: '/doc/rate', name: 'doc-rate', component: resolve => require(['docs/rate/index.vue'], resolve) },
    { path: '/doc/header-bar', name: 'doc-header-bar', component: resolve => require(['docs/header-bar/index.vue'], resolve) },
    { path: '/doc/field', name: 'doc-field', component: resolve => require(['docs/field/index.vue'], resolve) },

    /* project */
    { path: '/project/weather', name: 'project-weather', component: resolve => require(['projects/weather/index.vue'], resolve) },

    /* other */
    // { path: '/test', name: 'test', component: resolve => require(['demo/test.vue'], resolve) },
    { path: '*', name: '404', component: require('demo/404.vue') }
  ]
})

// router.beforeEach((to, from, next) => {
//   next()
// })

// router.afterEach((to, from, next) => {
//   console.log(to, from, next)
//   console.log(this)
// })

export default router



