import Tip from 'components/modal/tip.vue'

export default Vue => {
  const TipExtend = Vue.extend(Tip)
  let $vmTip = new TipExtend({
    el: document.createElement('div')
  })
  document.body.appendChild($vmTip.$el)

  Vue.prototype.$tip = (msg, delay = 1500, callback) => {
    $vmTip.show = true
    $vmTip.msg = msg
    setTimeout(() => {
      $vmTip.show = false
      callback && callback()
    }, delay)
  }
}



