<template lang="html">
  <!-- <div class="hd-alert" v-show="show">
    <div class="hd-alert-mask" @click="close(false)"></div>
    <div class="hd-alert-wrapper">
      <slot name="header">
        <div class="hd-alert-header" v-if="title" v-text="title"></div>
      </slot>
      <slot name="body">
        <div class="hd-alert-body" v-text="content"></div>
      </slot>
      <slot name="footer">
        <div class="hd-alert-footer">
          <button type="button" class="hd-alert-button hd-alert-button--cancel" v-if="cancelText" v-text="cancelText" @click="close(false)"></button>
          <button type="button" class="hd-alert-button hd-alert-button--confirm" v-if="okText" v-text="okText" @click="close(true)"></button>
        </div>
      </slot>
    </div>
  </div> -->
  <hd-dialog :show="show" @click-mask="close(false)">
    <div class="hd-alert">
      <slot name="header">
        <div class="hd-alert-header" v-if="title" v-text="title"></div>
      </slot>
      <slot name="body">
        <div class="hd-alert-body" v-text="content"></div>
      </slot>
      <slot name="footer">
        <div class="hd-alert-footer">
          <button type="button" class="hd-alert-button hd-alert-button--cancel" v-if="cancelText" v-text="cancelText" @click="close(false)"></button>
          <button type="button" class="hd-alert-button hd-alert-button--confirm" v-if="okText" v-text="okText" @click="close(true)"></button>
        </div>
      </slot>
    </div>
  </hd-dialog>
</template>

<script>
import HdDialog from './dialog.vue'

export default {
  name: 'hd-alert',
  components: {
    HdDialog
  },
  props: {
    show: Boolean,
    title: String,
    content: String,
    okText: {
      type: String,
      default: '确定'
    },
    cancelText: String
  },
  methods: {
    close (isConfirm) {
      this.$emit('click-alert', isConfirm)
    }
  }
}
</script>

<style lang="pcss">
@namespace hd {
  @b alert {
    width: 70%;
    word-wrap: break-word;
    font-size: 28px;
    color: #666;
    line-height: 1.5;
    text-align: center;
    border-radius: 6px;
    background-color: #fafafa;
    box-shadow: 0 19px 38px rgba(0, 0, 0, .3),
                0 15px 12px rgba(0, 0, 0, .22);

    @e header {
      font-size: 32px;
      font-weight: bold;
      color: #333;
      padding: 20px 24px 0;
      margin-bottom: -12px;
    }

    @e body {
      font-size: 28px;
      padding: 36px 24px;
    }

    @e footer {
      border-top: 1px solid #ccc;
      display: flex;
    }

    @e button {
      font-size: 28px;
      background-color: inherit;
      padding: 20px 0;
      flex: 1;

      @m cancel {
        color: #999;
        border-right: 1px solid #ccc;
      }

      @m confirm {
        color: #2db7f5;
      }
    }
  }
}
</style>



