<template lang="html">
  <header class="hd-header-bar">
    <div class="hd-header-bar-back" @click="back">
      <svg-left color="#eee" size="0.4"></svg-left>
    </div>
    <h1 class="hd-header-bar-title" v-text="title"></h1>
    <slot></slot>
  </header>
</template>

<script>
import SvgLeft from 'components/svg/left.vue'

export default {
  name: 'hd-header-bar',
  components: {
    SvgLeft
  },
  props: {
    title: String
  },
  methods: {
    back () {
      window.history.back()
    }
  }
}
</script>

<style lang="pcss">
@namespace hd {
  @b header-bar {
    text-align: center;
    line-height: 90px;
    min-height: 90px;
    color: #eee;
    background-color: #474a4f;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 999;

    @e back {
      width: 90px;
      position: absolute;
      top: 0;
      left: 0;
    }

    @e title {
      font-size: 36px;
      font-weight: normal;
    }

    a {
      font-size: 24px;
      padding: 0 20px;
      position: absolute;
      top: 0;
      right: 0;
    }
  }
}
</style>



