<template lang="html">
  <transition name="hd-dialog-fade">
    <div v-show="show" :class="['hd-dialog', 'is-' + position]">
      <div class="hd-dialog-mask" @click="clickmask" :style="{ backgroundColor: bg }"></div>
      <transition :name="'hd-dialog-fade-body-' + position">
        <div v-show="show" class="hd-dialog-body">
          <slot></slot>
        </div>
      </transition>
    </div>
  </transition>
</template>

<script>
export default {
  name: 'hd-dialog',
  props: {
    show: Boolean,
    position: {
      type: String,
      default: 'center'
    },
    bg: String
  },
  methods: {
    clickmask () {
      this.$emit('click-mask')
    }
  }
}
</script>

<style lang="pcss">
@namespace hd {
  @b dialog {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 2000;
    background-color: transparent;
    display: flex;

    @when center {
      align-items: center;
    }

    @when top {
      align-items: flex-start;
    }

    @when bottom {
      align-items: flex-end;
    }

    @e mask {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: 2001;
      background-color: rgba(0, 0, 0, 0.5);
    }

    @e body {
      width: 100%;
      z-index: 2002;
      display: flex;
      justify-content: center;
      align-items: center;
    }

    @e fade {
      @e enter {
        opacity: 1;

        @e active {
          transition: opacity .2s linear;
        }
      }

      @e leave {
        @e active {
          opacity: 1;
          transition: opacity .2s linear;
        }
      }

      @e body {
        @e center {
          @e enter {
            transform: scale(0);

            @e active {
              transition: transform .2s linear;
            }
          }

          @e leave {
            @e active {
              transform: scale(0);
              transition: transform .2s linear;
            }
          }
        }

        @e bottom {
          @e enter {
            transform: translateY(100%);

            @e active {
              transition: transform .2s linear;
            }
          }

          @e leave {
            @e active {
              transform: translateY(100%);
              transition: transform .2s linear;
            }
          }
        }

        @e top {
          @e enter {
            transform: translateY(-100%);

            @e active {
              transition: transform .2s linear;
            }
          }

          @e leave {
            @e active {
              transform: translateY(-100%);
              transition: transform .2s linear;
            }
          }
        }

        @e left {
          @e enter {
            transform: translateX(-100%);

            @e active {
              transition: transform .2s linear;
            }
          }

          @e leave {
            @e active {
              transform: translateX(-100%);
              transition: transform .2s linear;
            }
          }
        }

        @e right {
          @e enter {
            transform: translateX(100%);

            @e active {
              transition: transform .2s linear;
            }
          }

          @e leave {
            @e active {
              transform: translateX(100%);
              transition: transform .2s linear;
            }
          }
        }
      }
    }
  }
}
</style>



