<template lang="html">
  <div v-show="show" class="hd-loading">
    <div class="hd-loading-core">
      <svg-loading size="0.8"></svg-loading>
    </div>
    <p class="hd-loading-text" v-if="text" v-text="text"></p>
  </div>
</template>

<script>
import SvgLoading from 'components/svg/loading.vue'

export default {
  name: 'hd-loading',
  components: {
    SvgLoading
  },
  props: {
    show: Boolean,
    text: {
      type: String,
      default: '加载中...'
    }
  }
}
</script>

<style lang="pcss">
@namespace hd {
  @b loading {
    position: fixed;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    width: 160px;
    height: 160px;
    background-color: #5d5d5d;
    border-radius: 8px;
    z-index: 1000;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    @e text {
      font-size: 24px;
      color: #eee;
      margin-top: 10px;
    }
  }
}
</style>



