import FastClick from 'fastclick'
FastClick.attach(document.body)

import Vue from 'vue'
import App from './App.vue'
import router from './router'
import vueResource from 'vue-resource'
import vueInterceptors from 'plugins/interceptors'
import vueLoading from 'plugins/loading'
import vueModal from 'plugins/modal'

Vue.use(vueResource)
Vue.use(vueInterceptors)
Vue.use(vueLoading)
Vue.use(vueModal)

/* eslint-disable no-new */
new Vue({
  el: '#Application',
  router: router,
  render: h => h(App)
})



