import Alert from 'components/modal/alert.vue'

export default Vue => {
  const AlertExtend = Vue.extend(Alert)
  let $vmAlert = new AlertExtend({
    el: document.createElement('div')
  })
  document.body.appendChild($vmAlert.$el)

  $vmAlert.close = (isConfirm) => {
    $vmAlert.show = false
    $vmAlert.callback && $vmAlert.callback(isConfirm)
  }

  Vue.prototype.$alert = (any, callback) => {
    $vmAlert.show = true
    if (typeof any === 'string') {
      $vmAlert.title = ''
      $vmAlert.content = any
    } else {
      $vmAlert.title = any.title || ''
      $vmAlert.content = any.content || ''
    }
    $vmAlert.okText = any.okText || '确定'
    $vmAlert.cancelText = any.cancelText || ''
    $vmAlert.callback = callback
  }
}



