function isJson (any) {
  if (typeof any !== 'string') {
    return false
  }
  return /^\{(.+:.+,*){1,}\}$/.test(any)
}

export default Vue => {
  Vue.http.options.emulateJSON = true
  Vue.http.options.interceptor = true

  Vue.http.interceptors.push((req, next) => {
    next(res => {
      if (req.interceptor) {
        if (isJson(res.body)) {
          res.body = JSON.parse(res.body)
        }
      }

      console.info(req.method, '\n', req.url, '\n', res.body)
      return res
    })
  })
}



