<template lang="html">
  <div id="Application">
    <header-bar v-show="showHeaderBar" :title="headerBarTitle">
      <router-link :to="{ name: 'index' }">
        <svg-home color="#ccc" size="0.4"></svg-home>
      </router-link>
    </header-bar>
    <transition name="view-fade">
      <router-view class="view"></router-view>
    </transition>
  </div>
</template>

<script>
import HeaderBar from 'components/bar/header-bar.vue'
import SvgHome from 'components/svg/home.vue'

export default {
  name: 'application',
  components: {
    HeaderBar,
    SvgHome
  },
  data () {
    return {
      showHeaderBar: true,
      headerBarTitle: ''
    }
  },
  watch: {
    '$route.name' (newName) {
      this.renderHeader(newName)
    }
  },
  methods: {
    renderHeader (name = '') {
      // if (/^project\-/.test(name)) {
      //   this.showHeaderBar = false
      // } else {
      //   this.showHeaderBar = true
      // }
      this.headerBarTitle = name
    }
  },
  beforeMount () {
    this.renderHeader(this.$route.name)
  }
}
</script>

<style lang="pcss">
@b view {
  position: relative;
  margin-top: 90px !important;

  @e fade {
    @e enter {
      transform: translateX(100%);

      @e active {
        transition: transform .3s;
      }
    }

    /*@e leave {
      @e active {
        transform: translateX(100%);
        transition: transform .2s;
      }
    }*/
  }
}
</style>



